<template>
    <ul>
        <li v-for='item in imgs'><img :src='item.url'><span>url:{{item.url}}</span></li>
    </ul>
</template>
<script>
export default {
    data (){
        return {
            imgs:[]
        }
    },
    created() {
        this.$axios.get('selectImg').then(res => {
            if(res.status == 200){
                var d = res.data.data;
                for(var i in d){
                    d[i]['url'] = 'http://localhost:12306\\' + d[i].path + '.' + d[i].type
                }
                this.imgs = d
            }
        })
    },
}
</script>